import React from 'react';
import {Modal,Image,Header, Card, Divider } from 'semantic-ui-react';
import './Experience.css';

const description = [
    "This is a Card",
    "click on this card"
  ].join(' ');

export default class Experience extends React.Component {
render(){
    return (
        <div>
            <Card.Group>
                <Modal trigger={
                    <Card href="#">
                        <Card.Content header='Web Developer' />
                        <Card.Meta content="&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Vishv Infotech (Aug 2016 - Mar 2017)" />
                        <Card.Content description={description} />
                        <Card.Content extra /> 
                    </Card>
                }>
                <Modal.Header>Web Developer</Modal.Header>
                <Modal.Content image>
                    <Modal.Description>
                        <Header>Vishv Infotech (Aug 2016 - Mar 2017)</Header>
                        <p>Work in progrss</p>
                    </Modal.Description>
                </Modal.Content>
                </Modal>
                <Divider horizontal/>
                <Modal trigger={
                    <Card href="#">
                        <Card.Content header='Student Intern (java)' />
                        <Card.Meta content="&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;BISAG(Bhaskaracharya Institute for space &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;and Geo-Informatics) (June 2015 - June &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2016)" />
                        <Card.Content description={description} />
                        <Card.Content extra />
                    </Card>
                }>
                <Modal.Header>Student Intern (java)</Modal.Header>
                <Modal.Content image>
                    <Modal.Description>
                        <Header>BISAG(Bhaskaracharya Institute for space and Geo-Informatics) (June 2015 - June 2016)</Header>
                        <p>Work in progrss</p>
                    </Modal.Description>
                </Modal.Content>
                </Modal>
            </Card.Group>
        </div>
    );
}
}

